import { Component, OnInit, ViewChild } from '@angular/core';
import { Platform, NavParams, ViewController, Slides, ModalController, ActionSheetController } from 'ionic-angular';

import { PhotoService } from './../../services/photo.service';
import { MapModalComponent } from './../map-modal/map-modal.component';
import { Address } from './../../models/address.model';


@Component({
    templateUrl: './create-event-modal.component.html'
})
export class CreateEventModalComponent implements OnInit {

    currentDate: string;
    maxDate: string;

    startDate: any;
    startTime: any;

    @ViewChild(Slides) slides: Slides;
    activeSlideIndex = 0;

    tag: string;
    tags: string[] = [];

    place: Address = new Address();

    coverPicture = '';

    constructor(
        public platform: Platform,
        public params: NavParams,
        public viewCtrl: ViewController,
        public modalCtrl: ModalController,
        public actionSheetCtrl: ActionSheetController,
        private photoService: PhotoService
    ) { }

    ngOnInit() {
        this.tags = [];
        let date = new Date();
        let month = (date.getMonth() + 1) < 10 ? '0' + (date.getMonth() + 1) + '' : (date.getMonth() + 1);
        this.currentDate = [date.getFullYear(), month, date.getDate()].join('-');
        this.maxDate = [date.getFullYear() + 1, 12, 31].join('-');
    }

    dismissModal() {
        this.viewCtrl.dismiss();
    }

    dateChange() {
        console.log(this.startDate);
    }

    timeChange() {
        console.log(this.startTime);
    }

    slideChanged() {
        this.activeSlideIndex = this.slides.getActiveIndex();
    }

    addTag() {
        this.tags.push(this.tag.trim());
        this.tag = null;
    }

    deleteTag(i: number) {
        this.tags.splice(i, 1);
    }

    presentMapModal() {

        let mapModal = this.modalCtrl.create(MapModalComponent, { location: this.place });
        mapModal.onDidDismiss(selectedPlace => {
            if (selectedPlace === null) {
                console.log('place stays the same');
            } else {
                console.log('place has been chosen');
                this.place = JSON.parse(JSON.stringify(selectedPlace));
            }
            console.log('returned location', this.place);
        });
        mapModal.present();

    }

    presentPhotoActionSheet() {

        let actionSheet = this.actionSheetCtrl.create({
            title: 'Ajouter une photo de couverture',
            buttons: [
                {
                    text: 'Choisir à partir du Gallery',
                    icon: 'md-images',
                    role: 'destructive',
                    handler: () => {
                        this.photoService.takePhoto(0, 600, 500).then(
                            (imageData) => {
                                this.coverPicture = 'data:image/jpeg;base64,' + imageData;
                            },
                            (error) => {
                                console.log('something wrong', error);
                            }
                        );
                    }
                },
                {
                    text: 'Prendre une photo',
                    icon: 'md-camera',
                    role: 'destructive',
                    handler: () => {
                        this.photoService.takePhoto(1, 600, 500).then(
                            (imageData) => {
                                this.coverPicture = 'data:image/jpeg;base64,' + imageData;
                            },
                            (error) => {
                                console.log('something wrong', error);
                            }
                        );
                    }
                }
            ]
        });

        actionSheet.present();

    }

}


